                            <span>{{ item.date }}</span>
                        </template> -->
                        <!-- <template v-slot:item.actions="{ item }"> -->
                            <v-icon small class="mr-2" @click="dialogOpen(item, Edit_Save_Flag=false)">
                            mdi-pencil
                            </v-icon>
                            <v-icon small @click="dialogDelOpen(item)">
                            mdi-delete
                            </v-icon>
                        <!-- </template> -->
                    </v-data-table>
                </v-card-text>
            </v-card>
            </v-col>
        </v-row>

        <v-dialog v-model="dialogFlag" max-width="500px">
