                    <v-text-field dark v-model="defaultItem.date" :disabled = true label="년도/날자"> </v-text-field>
                </v-col>
                </v-row>
                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue-grey lighten-1" dark @click="dialog_Save">OK</v-btn>
                <v-btn color="blue-grey lighten-1" dark @click="dialog_Coles">CANCEL</v-btn>
                </v-card-actions>
            </v-container>
            </v-card-text>       
        </v-card>
        </v-dialog>
        
