<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'YoungVuew',
  components: {
    //HelloWorld
  },

  data: () => ({
    dialogFlag: false,
    Edit_Save_Flag: false,
    dialogTitle: 'New Item',

    headers:[
