          text: '생산 품목',
          align: 'start',
          sortable: true,
          value: 'productname',
        },
        { text: '생산수량', value: 'Qty' },
        { text: '무게 (g)', value: 'weight' },
        { text: '부피 (g)', value: 'volume' },
        { text: '년도/날자/시간', value: 'date' },
        { text: 'Actions', value: 'actions', sortable: false },
        ],
        items:[],
        selectitems:[],
        defaultItem: {
          productname: '',
          Qty: 0,
          weight: 0,
          volume: 0,
          date: ''
        },
        NullItem: {
          productname: '',
          Qty: 0,
          weight: 0,
          volume: 0,
          date: ''
        },


    }),

    async mounted (){
      //await this.data_listup()
      // await this.all_Remove()
    },

    computed: {
    formTitle () {
      return this.dialogTitle
        }
    },

    methods: {
        dialogOpen(Edit_Save_Flag){
            if(Edit_Save_Flag==true){
                this.dialogTitle='New Item'
            }
            if(Edit_Save_Flag==false){
                // this.defaultItem = item
                this.dialogTitle='Edit Item'
            }
            this.dialogFlag = true
        },
    },
}
</script>

